import * as React from "react"
import Layout from "../components/layout"
import Seo from "../components/seo"
import InstallationPage from "../components/InstallationPage"
import installation from "../data/en/installation"

const Installation = ({
  location,
}: {
  location: {
    search: string
    pathname: string
    hash: string
  }
}) => (
  <Layout location={location} defaultLang="en">
    <Seo title={installation.title} location={location} />
    <InstallationPage location={location} defaultLang="en" />
  </Layout>
)

export default Installation
