export default function Header({
  homeRef,
  defaultLang,
}: // isCardPlay,
{
  homeRef: React.Ref<HTMLDivElement>
  defaultLang: string
  // isCardPlay: boolean
}) {
  const [isWeb, setIsWeb] = React.useState(true)
  const {
    state: { language },
  } = useStateMachine()
  const {
    state
  } = useStateMachine()
  const { currentLanguage } =
    language && language.currentLanguage
      ? language
      : { currentLanguage: defaultLang }
  const lightMode = state?.setting?.lightMode
  const getStarted = getStartedContent[currentLanguage]

  return (
    <>
      <AnimateGroup play>
        <header className={styles.head} id="main">
          <Animate
            sequenceIndex={0}
            duration={0.2}
            start={{
              transform: "translateY(20px)",
              opacity: 0,
            }}
            end={{
              transform: "translateY(0)",
            }}
            easeType="ease-in"
            render={({ style }) => (
              <h1
                className={`${typographyStyles.heading} ${styles.logoHeading}`}
                style={style}
              >
                {getStarted.installation}
              </h1>
            )}
          />

          <Animate
            sequenceIndex={1}
            start={{
              transform: "translateY(20px)",
              opacity: 0,
            }}
            end={{
              transform: "translateY(0)",
            }}
            easeType="ease-in"
            render={({ style }) => (
              <p
                className={typographyStyles.subHeading}
                style={{ marginBottom: 0, maxWidth: 660, ...style }}
              >
                {home.slogan[currentLanguage]}
              </p>
            )}
          />
          
        </header>
      </AnimateGroup>
      {/*<Bday isCardPlay={isCardPlay} />*/}
    </>
  )
}
